import React from "react";
import styled from "styled-components";

import { FlexBoxContentCenter } from "../FlexBox";

import { css } from "styled-components";

const column = css`
  display: block;
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: 25%;
  width: 25%;
`;
const rounded = css`
  border-radius: 5px;
`;
const Card = styled.div`
  ${rounded}
  ${column}
`;

const StyledHeaderTitle = styled(FlexBoxContentCenter).attrs({
  as: "h1",
})`
  width: 169px;
  height: 64px;

  background-color: ${(props) => props.theme.color.ultramarineBlue};

  color: #fff;
  font-size: 24px;
  font-weight: 700;
`;
export default function HeaderTitle(): JSX.Element {
  return (
    <StyledHeaderTitle>
      R.js
      <Card />
    </StyledHeaderTitle>
  );
}
